.table {
    display: flex;
    flex-direction: column;
    width: 100%;
}

.table-body {
    display: flex;
    flex-direction: row;
    height: 100%;
    border-collapse: collapse;
}

.table-header {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 15px;
}

.table-header__text {
    margin-bottom: 10px;
}
