.column-item {
    min-height: 50px;
    /* border-bottom: 1px solid grey; */
    /* box-shadow: 0px 0px 2px 2px grey; */
    display: flex;
    align-items: center;
    position: relative;
    border-bottom: 1px solid rgba(0,0,0,.12);
    /* box-shadow: 0 2px 4px 0 rgb(0 0 0 / 4%); */
    margin-bottom: 10px;
    justify-content: space-between;
    width: 100%;
}

.column-item:last-child {
    margin-bottom: 0px;
}



.column-item__button {
    background:  url(./../../Images/dustbin.svg) no-repeat;
    width: 20px;
    height: 20px;
    border: none;
    cursor: pointer;
}