.todo-list {
    text-align: center;
    justify-content: center;
    width: 100%;
}

.todo-list-header {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-bottom: 20px;
}

.todo-list__buttons-block {
    display: flex;
    flex-direction: row;
    gap: 10px;
}

.add-button-icon {
    background: url("./../../Images/add.svg") no-repeat;
    width: 24px;
    height: 24px;
}

.delete-button-icon {
    background: url("./../../Images/dustbin.svg") no-repeat;
    width: 24px;
    height: 24px;
}

.modal-content {
    display: flex;
    flex-direction: column;
    gap: 10px;
    align-items: center;
}

.modal-content input {
    min-width: 250px;
    height: 25px;
}