.item-list {
    padding: 10px;
    border: 1px solid lightgray;
    /* border-collapse: collapse; */
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    border-radius: 5px;
    cursor: pointer;
}

.item-list:hover {
    background: rgb(242, 227, 208, 0.2);
    border: 1px solid #f2e3d0;
}

.item-list {
    margin-bottom: 3px;
}

.item-list:last-child {
    margin-bottom: 0;
}

.item-list.done:hover {
    background: none;
}

.item-list.done {
    cursor: default;
}

.item-list_buttons {
    display: flex;
    gap: 10px;
}

.item-list_delete-button {
    background: url("./../../Images/dustbin.svg") no-repeat;
    width: 24px;
    height: 24px;
    border: none;
    cursor: pointer;
}

.item-list_edit-button {
    background: url("./../../Images/edit-icon.svg") no-repeat;
    width: 24px;
    height: 24px;
    border: none;
    cursor: pointer;
}
.item-list.done .item-list_name {
    text-decoration: line-through;
    opacity: 0.4;
}
.item-list .item-list_name {
    white-space: break-spaces;
    overflow-wrap: anywhere;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-align: left;
    align-items: center;
}

.item-list_done-button {
    background: url("./../../Images/done-icon.svg") no-repeat;
    width: 24px;
    min-width: 24px;
    height: 24px;
    border: none;
    cursor: pointer;
}

.item-list_circle-button {
    background: url("./../../Images/circle-icon.svg") no-repeat;
    width: 24px;
    height: 24px;
    min-width: 24px;
    border: none;
    cursor: pointer;
    text-align: start;
}

.item-list__title {
    display: flex;
    gap: 10px
}