.column {
    min-width: 200px;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-right: 20px;
    border: 1px solid rgba(0,0,0,.12);
    border-radius: 10px;
    box-shadow: 0 2px 4px 0 rgb(0 0 0 / 4%);
    padding: 10px;
    display: flex;
    flex-direction: column;
    height: fit-content;
    position: relative;
    cursor: grab;
}

.column:last-child {
    margin-right: 0px;
}

.column-header {
    margin-bottom: 10px;
    font-weight: bold;
    font-size: 20px;
}

.column__add-button {
    width: 100%;
    margin-bottom: 10px;
}

.column-delete-button {
    background: url("./../../Images/crosslinear.svg") no-repeat;
    width: 18px;
    height: 18px;
    border: none;
    cursor: pointer;
    position: absolute;
    right: 0;
    top: 0;
    margin: 5px;
    opacity: 0.5;
}

.column-delete-button:hover {
    opacity: 1;
}