.fc-day:not(.fc-col-header-cell):hover {
    background: rgb(242, 227, 208, 0.2);
    outline: 2px solid #f2e3d0;
}

.fc-day-today:not(.fc-col-header-cell) {
    background-color: rgb(242, 227, 208, 0.5) !important;
}

.fc-day:not(.fc-col-header-cell).selected{
    outline:  2px solid #ff9900;
    background-color: rgb(242, 227, 208, 0.5) !important;
}

.calendar-wrapper .fc-daygrid-day-frame {
    padding: 5px;
}

.calendar-wrapper .fc-daygrid-event-harness {
    margin-bottom: 2px;
    border: 1px solid rgb(0,0,0,0.1);
    border-radius: 4px;
    padding: 5px;
    overflow: hidden;
}

.calendar-wrapper .fc-daygrid-event-harness a {
    white-space: break-spaces;
    overflow-wrap: anywhere;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    overflow: hidden;
    
}
.calendar-wrapper .fc-daygrid-event-harness:hover {
    background: rgba(0,0,0,.1);
}
 .calendar-wrapper .fc-daygrid-dot-event.fc-event-mirror, .fc-daygrid-dot-event:hover {
    background: none;
 }



