.sidebar-panel {
    border-right: 1px solid rgba(0,0,0,.12);
    border-left: none;
    height: 100vh;
    width: 200px;
    background:#F8F8F8;
    overflow: hidden;
    transition: .2s;
    position: absolute;
    z-index: 100;
}

.sidebar-panel-button {
    width: 100%;
    min-width: max-content;
    height: 40px;
    border: none;
}

.sidebar-panel-button.select {
    box-shadow: inset 0 0 10px grey;
}

.sidebar-panel-button:hover {
    background: #E3E5E8;
}

.sidebar-panel.hide{
    width: 0;
}
