.frame-header {
    height: 40px;
    width: 100%;
    background: #F2D0D0;
    display: flex;
    flex-direction: row;
    align-items: center;
    text-align: center;
    opacity: 0.7;
    border-bottom: 1px solid #fff;
    box-shadow: 0 1px 4px #0000004d;
  }
  
  .frame-header-text {
    font-size: large;
    justify-content: center;
    width: 100%;
  }
  
  .frame-header-icon {
    background: url('./../../Images/menu-icon.svg') no-repeat;
    width: 24px;
    height: 24px;
    margin-left: 10px;
    cursor: pointer;
    position: absolute;
  }
